import moment from "moment";
import Chart from "./Chart.vue";
import Avatar from "./Avatar.vue";
import Copy from "./Copy.vue";
import sensors from "../sensors";

export default {
  props: ["sender", "sensor_id", "log", "model", "count", "type"],
  components: {
    Chart,
    Avatar,
    Copy,
  },
  data() {
    return {
      points: [],
    };
  },
  watch: {
    sensor_id: {
      immediate: true,
      handler: function (newValue, oldValue) {
        if (newValue !== oldValue) {
          let isData = false;
          for (const item of this.log) {
            for (const type of this.series) {
              if (item.data[type.options.name]) {
                isData = true;
                break;
              }
            }
          }
          if (this.model !== 1 && isData) {
            this.points = [...this.log];
          } else {
            this.points = [];
          }
        }
      },
    },
    log: {
      immediate: false,
      handler: function (newValue) {
        this.$nextTick(() => {
          if (this.$refs.chart) {
            const count =
              this.$refs.chart.$refs.chart.chart.series[0].points.length -
              newValue.length;
            if (count < 0) {
              let series = [this.type];
              if (this.type === "pm10" || this.type === "pm25") {
                series = ["pm10", "pm25"];
              }
              const newPoints = newValue.slice(count);
              for (const i in series) {
                for (let point of newPoints) {
                  this.$refs.chart.addPoint(i, [
                    Number(point.timestamp),
                    Number(point.data[series[i]]),
                  ]);
                }
              }
            }
          }
        });
      },
    },
  },
  computed: {
    link: function () {
      return sensors[this.sensor_id] ? sensors[this.sensor_id].link : "";
    },
    countTx: function () {
      return this.count;
    },
    last: function () {
      return this.log[this.log.length - 1];
    },
    date: function () {
      return moment(this.last.timestamp, "X").format("DD.MM.YYYY HH:mm:ss");
    },
    series: function () {
      if (this.type === "pm10" || this.type === "pm25") {
        return [
          {
            name: "PM10",
            color: "#e8b738",
            lineWidth: 1,
            data: [],
            options: {
              name: "pm10",
            },
          },
          {
            name: "PM2.5",
            color: "#89b268",
            lineWidth: 1,
            data: [],
            options: {
              name: "pm25",
            },
          },
        ];
      }
      return [
        {
          name: this.type,
          color: "#e8b738",
          lineWidth: 1,
          data: [],
          options: {
            name: this.type,
          },
        },
      ];
    },
  },
};
