import substrate from "../utils/substrate";

export default {
  data() {
    return {
      status: null,
      block: null,
      time: null,
    };
  },
  async mounted() {
    await substrate.init({
      endpoint: "wss://kusama.rpc.robonomics.network/",
      types: {},
    });

    await substrate.api.rpc.chain.subscribeNewHeads(async (header) => {
      this.time = (await substrate.api.query.timestamp.now()).toNumber();
      this.block = header.number;
    });

    this.upStatus();
  },
  computed: {
    timeFormat: function () {
      return new Date(this.time).toLocaleString();
    },
  },
  methods: {
    upStatus() {
      const currentTime = Date.now();
      if (this.time + 15000 < currentTime) {
        this.status = false;
      } else {
        this.status = true;
      }
      setTimeout(() => {
        this.upStatus();
      }, 1000);
    },
  },
};
