import config from "../config";
export default {
  props: ["current"],
  data() {
    return {
      isConnectionRemote: null,
      settingsType: null,
      remote_placeholder: config.REMOTE_PROVIDER,
      settings: {
        remote: {
          url: config.REMOTE_PROVIDER,
        },
      },
    };
  },
  created() {
    const settings = localStorage.getItem("settings") || null;
    if (settings) {
      try {
        this.settings = JSON.parse(settings);
      } catch (_) {
        console.warn("error", settings);
      }
    }
    if (this.current == "remote") {
      this.viewSettings("remote");
      setInterval(() => {
        if (this.$provider) {
          this.isConnectionRemote = this.$provider.connection;
        }
      }, 1000);
    }
  },
  methods: {
    viewSettings(type) {
      this.settingsType = type;
    },
    setSetting(type) {
      if (type === "remote") {
        localStorage.setItem("settings", JSON.stringify(this.settings));
        this.set(type);
      }
    },
    set(type) {
      this.$router
        .push({
          name: "main",
          params: {
            provider: type,
            type: this.$route.params.type,
            zoom: this.$route.params.zoom,
            lat: this.$route.params.lat,
            lng: this.$route.params.lng,
          },
        })
        .catch(() => {});
      window.location.reload(false);
    },
  },
};
